<template>
	<view class="flex flex-direction">
		<!-- 是否登录判断 -->
		<is-login v-if="isLoginShow" :currentPage="currentPage"></is-login>
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">切换组织</block>
		</cu-custom>
		<uni-list>
			<uni-list-item showSwitch
				:thumb="(item.Logo == null || item.Logo == '' || item.Logo == 'string') ? '/static/organ/organ_default.png': item.Logo"
				thumbSize="lg" v-for="(item, index) in organ_list" :key="index">
				<template slot="body" style="width: 60%;">
					<navigator :url="'/pages/basic/orgation/orgationdetail?preview=true&MchId=' + item.MchId">
						<navigator v-if="item.IsAdmin" class=" cf " :url="'/pages/basic/system/home?preview=true&MchId=' + item.MchId ">
							<view class="fl text-bold text-cut text-lg" >{{ item.Name }}</view>
							<view class="fr padding-sm cu-tag text-sm margin-right">{{ '管理' }}</view>
						</navigator>
						<view v-else class="text-bold text-cut text-lg">{{ item.Name }}</view>
						<view class=" cf ">
							<view class="fl text-sm padding-top-xs">
								{{ item.Area1Name }} {{(item.Area1Name == null|| item.Area1Name == '') ? '' :'-'}}
								{{ item.Area2Name }} {{(item.Area2Name == null|| item.Area2Name == '') ? '' :'-'}}
								{{ item.Area3Name }}
							</view>
							<view class="text-sm fr cu-tag">{{ item.IndustryName }}</view>
						</view>
						<view class="text-sm text-cut text-grey">联系方式：{{ item.LinkTel ? item.LinkTel : '' }}</view>
						<view class="text-sm text-cut text-grey">地址：{{ item.Address }}</view>
					</navigator>
				</template>
				<template slot="footer" style="width: 20%;">
					<view class="flex solid-bottom padding justify-center">
						<view v-if="MchId == item.MchId" class=" padding-sm cu-tag margin-xs bg-cyan">{{ '主企业'  }}
						</view>
						<view v-else class=" padding-sm cu-tag margin-xs bg-cyan light" @click="changeMch"
							:data-id="item.MchId" :data-name="item.Name">{{ '设为主企业' }}</view>
					</view>
				</template>
			</uni-list-item>
		</uni-list>
		<!-- 跳过此步骤 -->
		<navigator v-if="currentPage != 'index'" class="flex flex-direction" url="" @tap="toSkip">
			<button class="margin padding-lg bg-grey cu-btn round" >跳过此步骤</button>
		</navigator>
		<!-- 悬浮按钮 -->
		<navigator url="/pages/basic/orgation/orgation_create?preview=true">
			<uni-fab ref="fab" :pattern="pattern" horizontal="left" vertical="bottom" direction="horizontal" />
		</navigator>
	</view>
</template>

<script>
	/**
	 * 切换当前组织
	 * @author 陆彦捷
	 * @date 2021-07-05 1. 悬浮按钮变为只添加（移除编辑、组织管理、添加新成员的按钮） 2. 修改切换主企业按钮样式 。 切换主企业前添加提示 3. 在列表页，根据是否管理员，设置管理页面
	 * @date 2021.07.07 添加跳过此步骤按钮
	 * @date 2021.07.08 设置跳过此步骤方法改为本地缓存
	 * @date 201.07.23 重新引用login
	 */
	import organ from '@/common/storage/organstorage.js';
	var _self;
	export default {
		data() {
			return {
				// 页面样式参数
				preview: false, // 是否返回上级页面
				isLoginShow : false ,	// 验证登录控件是否登录
				currentPage : '',	// 当前页面
				pattern: {
					color: '#7A7E83', // 文字默认颜色
					selectedColor: '#00BEB7',
					backgroundColor: '#fff',
					buttonColor: '#00BEB7'
				}, // 可选配置项样式（悬浮按钮）
				content: [{
						iconPath: '/static/fabIcon/add.png',
						selectedIconPath: '/static/fabIcon/addHL.png',
						text: '新建组织',
						active: true
					},

				], // 展开菜单配置项
				// 系统数据相关
				organ_list: organ.getmyOrgans(), // 获取用户当前组织
				Token: this.global.login.getToken(), // 当前Token
				UserId: this.global.login.getUserId(), // 当前用户Id
				MchId: organ.getIndustryId(), // 获取当前组织（默认当前的组织值为1）
			}
		},
		onLoad: function(options) {
			_self = this;
			this.getParams(options); // 获取参数
			this.isLoginShow = true;	// 启用验证是否登录
			if (this.MchId == '' || this.MchId == 0) {
				this.mchId_request(); // 设置当前组织id
			}
			if (this.organ_list == null || this.organ_list == '' || this.organ_list == []) {
				this.userMchs_request();
			}
		},
		methods: {
			// =========================================== 初始化方法 ===============================================
			/** 获取页面初始化时参数
			 * @param {Object} options 页面传参
			 */
			getParams(options) {
				this.preview = options.preview != 'false' ? Boolean(options.preview) : false;
				this.currentPage = options.currentPage ? options.currentPage : 'index';		 // 当前页面属性
			},
			// =========================================== 监听事件 =================================================
			/**
			 * 切换主企业
			 * @param {Object} e  切换主企业的相关参数 
			 */
			changeMch(e) {
				uni.showModal({
					title: '设置',
					content: '将' + e.currentTarget.dataset.name + '设置为主企业（注：设置将立即生效，请谨慎选择）',
					success: function(result) {
						if (result.confirm) {
							// 切换当前企业为主企业
							_self.MchId = e.currentTarget.dataset.id;
							_self.changeMch_request(e.currentTarget.dataset.id, e.currentTarget.dataset.name);
						}
					}
				});
			},
			/**
			 * 跳过此步骤
			 * @param {Object} e
			 */
			toSkip : function(e){
				this.global.login.setSkipTrue();	// 设置跳过此步骤为true
				// 跳转到主页
				uni.navigateTo({
					url :'/pages/index/index'
				});
			},
			// ============================================ 请求 ====================================================
			/**
			 * 获取当前的组织列表
			 */
			userMchs_request() {
				uni.showLoading();
				uni.request({
					url: _self.website + '/api/SvcMerchant/GetUserMchs',
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						uni.hideLoading();
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								_self.organ_list = result.data.Data;
								organ.setmyOrgans(result.data.Data);
							}
						}
					},
					fail: function(erros) {
						uni.hideLoading();
						_self.organ_list = [];
					}
				})
			},
			/**
			 * 获取当前组织的主企业
			 */
			mchId_request() {
				uni.request({
					url: _self.website + '/api/SvcMerchant/GetDefMch',
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								organ.setIndustryId(result.data.Data.MchId);
								_self.MchId = result.data.Data.MchId;
								return;
							}
						}
						_self.MchId = 0
					},
					fail: function(erros) {
						console.log("fail", erros);
						_self.MchId = 0
					}
				})
			},
			/** 切换当前企业为主企业
			 * @param {int} mchId 组织Id
			 * @param {string} name 当前组织名称
			 */
			changeMch_request(mchId, name) {
				uni.request({
					url: _self.website + '/api/SvcUser/ChangeMch?mchId=' + mchId,
					header: {
						Authorization: 'Bearer ' + _self.Token,
					},
					method: 'POST',
					success: function(result) {
						console.log("切换主企业成功", result);
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								var data = result.data.Data;
								// 设置Local当前组织值
								organ.setIndustryId(mchId);
								organ.setName(name);
							}
						}
					},
					fail: function(erros) {
						console.log("切换主企业失败", erros);
					}
				})
			}
			// ==================================================================================================================	
		}
	}
</script>

<style>

</style>
